[data-theme="dark"]:root {
  --drk-bodybg: #18191a;
  --drk-primarytext: #eaeaea;
  --drk-secondarytext: #b0b3b8;
  --drk-secondarybg: #242526;
  --drk-thirdbg: #404040;
  --drk-menutext: #e1e1e1;
  --drk-bordercolor: #535353;
  --drk-whitecolor: #fff;
