}

.dm-center.tm-dark {
  background: url(https://akm-img-a-in.tosshub.com/sites/aajtak/images/dt-btn-txt-darkmode.png)
    center bottom no-repeat;
}

.theme-toggle {
  margin: 0 auto;
}

.darkmode-coachmark input[type="checkbox"] {
  height: 0;
  width: 0;
  visibility: hidden;
}

.darkmode-coachmark label {
  cursor: pointer;
  text-indent: -9999px;
  width: 112px;
  height: 37px;
  line-height: 35px;
  background: #000;
  float: left;
  left: 75px;
  border-radius: 100px;
  border: 1px solid #7d7c7c;
  position: relative;
}

.darkmode-coachmark label:after {
  content: "";
  position: absolute;
  top: 4px;
  left: calc(100% - 4px);
  width: 28px;
  height: 28px;
  border-radius: 90px;
  transition: 0.3s;
  -webkit-transform: translateX(-100%);
  -ms-transform: translateX(-100%);
  -o-transform: translateX(-100%);
  transform: translateX(-100%);
  background: #fff
    url(data:image/svg+xml;base64,)
    center center no-repeat;
  background-size: 18px;
}

.darkmode-coachmark input:checked + label {
  background: #fff;
}

.darkmode-coachmark input:checked + label:after {
  left: 5px;
  -webkit-transform: translateX(0);
  -ms-transform: translateX(0);
  -o-transform: translateX(0);
  transform: translateX(0);
  background: #000
    url(data:image/svg+xml;base64,)
    center center no-repeat;
  background-size: 20px;
}

.darkmode-coachmark label:active:after {
  width: 70px;
}

.darkmode-coachmark label:before {
  content: "Dark Mode";
  position: absolute;
  top: 0;
  color: #fff;
  font-family: "Noto Sans", sans-serif;
  font-size: 12px;
  font-weight: 400;
  text-indent: 0;
  left: 0;
  padding-left: 8px;
  width: 100%;
  white-space: nowrap;
  text-align: left;
}

.darkmode-coachmark input:checked + label:before {
  content: "Light Mode";
  color: #000;
  text-align: right;
  padding-left: 0;
  padding-right: 8px;
  left: auto;
  right: 0;
}

.second-level-menu.menu li label,
ul#newlist li label {
  text-indent: 0;
  background: 0 0;
  border: none;
  border-radius: 0;
  left: 0;
  float: none;
  font-size: 16px;
  line-height: 18px;
  color: #fff;
  font-weight: 400;
  position: relative;
  width: 100%;
  display: flex;
  height: 32px;
  align-items: center;
  padding: 0 10px;
}

.second-level-menu.menu li label:before,
ul#newlist li label:before {
  content: "";
  top: 8px;
  left: auto;
  right: 5px;
  position: absolute;
  background: #fff;
  font-size: 12px;
  font-weight: 400;
  text-indent: 0;
  padding-left: 8px;
  width: 28px;
  height: 16px;
  white-space: nowrap;
  text-align: left;
  border-radius: 22px;
}

.second-level-menu.menu li label:after,
ul#newlist li label:after {
  content: "";
  height: 22px;
  width: 22px;
  top: 5px;
  background: #e32000
    url(data:image/svg+xml;base64,)
    center center no-repeat;
  background-size: 14px;
  left: auto;
  right: -1px;
  transition: 0.3s;
  position: absolute;
  border-radius: 90px;
  -webkit-transform: translateX(-100%);
  -ms-transform: translateX(-100%);
  -o-transform: translateX(-100%);
  transform: translateX(-100%);
}

[data-theme="dark"] .second-level-menu.menu li label:after,
[data-theme="dark"] ul#newlist li label:after {
  background: #7c7c7c
    url(data:image/svg+xml;base64,)
    center center no-repeat;
  background-size: 12px;
  left: auto;
  right: -18px;
}

ul#newlist li label {
  cursor: pointer;
}

.second-level-menu.menu li label span,
ul#newlist li label span {
  pointer-events: none;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 1;
}

.second-level-menu.menu li label span:after,
ul#newlist li label span:after {
  content: "";
  padding-left: 3px;
}

[data-theme="dark"] .second-level-menu.menu li label span:after,
[data-theme="dark"] ul#newlist li label span:after {
  content: "";
}

.second-level-menu.menu li.userlogin-icon-parent-mobile svg {
  width: 22px;
}

@media (max-width: 768px) {
  #block-itg-layout-manager-header-block .navigation .second-level-menu.menu {
    display: flex;
    flex-direction: column;
  }

  #block-itg-layout-manager-header-block
    .navigation
    .second-level-menu.menu
    li.userlogin-icon-parent-mobile {
    order: 1;
  }

  #block-itg-layout-manager-header-block
    .navigation
    .second-level-menu.menu
    li.search-icon-parent-mobile {
    order: 2;
  }

  #block-itg-layout-manager-header-block
    .navigation
    .second-level-menu.menu
    li.drkmode {
    order: 3;
  }

  #block-itg-layout-manager-header-block
    .navigation
    .second-level-menu.menu
    li {
    order: 4;
  }

  .second-level-menu.menu li label {
    color: #666;
    height: 38px;
    padding-left: 15px;
  }

  .second-level-menu.menu li label:before {
    box-shadow: inset 0 0 0 1.5px #8c8c8c;
  }

  .second-level-menu.menu li label:before {
    top: 11px;
  }

  .second-level-menu.menu li label:after {
    top: 8px;
  }

  [data-theme="dark"] .second-level-menu.menu li label {
    color: #eaeaea;
  }
}
