.goToTop {
  width: 50px;
  height: 50px;
  background: #f74600;
  -moz-border-radius: 50px;
  -webkit-border-radius: 50px;
  border-radius: 50px;
  -webkit-box-shadow: 3px 3px 5px 0 rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 3px 3px 5px 0 rgba(0, 0, 0, 0.5);
  box-shadow: 3px 3px 5px 0 rgba(0, 0, 0, 0.5);
  cursor: pointer;
  display: inline-block;
  position: fixed;
  bottom: 100px;
  right: 10px;
  z-index: 9999;
  transform: scale(0);
  float: right;
  -webkit-transition: all 0.3s linear;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.goToTop.showGototop {
  transform: scale(1);
}

.goToTop:hover {
  background: #bf3c08;
  transition: all 0.3s ease;
}

.goToTopIcon {
  margin: auto;
  left: 1px;
  right: 0;
  top: 0px;
  border: 0;
  bottom: 0;
  position: absolute;
  height: 48px;
  width: 48px;
}

.goToTopIcon .icon-assets-dropdown-arrow-close {
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  color: #fff;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.goToTopIcon .icon-assets-dropdown-arrow-close:before {
  content: "\6e";
}

.goToTopIcon i {
  color: #fff;
  font-size: 24px;
  line-height: 23px;
}

