.movie_detail_main_div{
    display: flex; 
    justify-content: flex-end;
     width: 40%;
}
.section_div{
    justify-content: center;
}
.newsletter_div{
    width:100%;
    display:flex;
    margin-top:10px;
   
}
@media (max-width: 1000px){
   .side_main_div {
    display: none;
    }
    .movie_detail_main_div{
      
         width: 90%;
    }
    .section_div{
        justify-content: center;
    }
}
@media (max-width: 750px){
    .newsletter_div{
        flex-direction: column;
    }
    }
@media (min-width: 768px){
.col-md-8 {
    -ms-flex: 0 0 66.666667%;
     flex: 0 0 66.666667%; 
     /* max-width: 66.666667%;  */
}
}
.side_main_div ul li:hover{
    text-decoration: underline;
    
}
.side_main_div ul li{
    border-bottom: 1px solid rgb(242, 242, 242);
    padding: 10px;
    cursor: pointer;
}
.side_main_div h1,
.side_main_div h2,
.side_main_div h3,
.side_main_div h4,
.side_main_div h5,
.side_main_div h6{
    padding: 10px;
}
.side_main_div ul{
    list-style-type: none;
}
#block-itg-layout-manager-header-block .navigation{
    overflow: inherit !important;
}
 
.dropbtn {
    color: white;
    font-size: 16px;
    border: none;
  }
  
  .mine_dropdown {
    position: relative;
    display: inline-block;
    cursor: pointer;
  }
  
  .mine_dropdown-content {
    display: none;
    position: absolute;
    background-color: #a00606;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    flex-direction: column;
  }
  
  .mine_dropdown-content a::after {
    display: none;
  }
  .mine_dropdown-content a {
    color: black ;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    border-bottom: 1px solid black;
    width: 100%;
  }
  
  .mine_dropdown-content a:hover {background-color: #ddd;}
  
  .mine_dropdown:hover .mine_dropdown-content {display: flex;}

  .itg-common-section{
      display:flex;
      flex-wrap: wrap;
      flex-direction: row;
  }
  